<template>
    <div class="home-index">
        <div class="search">
            <div class="logo">
                <img src="../../assets/home/logo.png" alt="">
            </div>
            <div class="input" @click="$routerPush('/search/index')">
                <div class="ico">
                    <img src="../../assets/home/search.png" alt="">
                </div>
                <div class="text">想看什么就点我</div>
            </div>
            <div class="history" @click="$routerPush('/user/history')">
                <img src="../../assets/home/history.png" alt="">
            </div>
        </div>
        <div class="banner">
            <div id="swiper" class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item,index) in banner.data" :key="index" @click="bannderHandel(item.type,item.value)">
                        <img :src="item.image" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="navigation">
            <div class="item" v-for="(item,index) in navigation.data" :key="index" @click="navigationHandel(item.type,item.value,item.title)">
                <div class="ico">
                    <img :src="item.image"/>
                </div>
                <div class="text">{{item.title}}</div>
            </div>
        </div>

        <div class="recommend">
            <div class="x-title">
                <div class="title">
                    推荐视频
                </div>
            </div>
            <div class="box">
                <div class="item" v-for="(item,index) in recommend.data" :key="index" @click="videoHandel(item.uuid)">
                    <div class="image">
                        <img :src="item.thumbnail">
                    </div>
                    <div class="title">{{ item.translate_title }}</div>
                </div>
            </div>
        </div>

        <div class="prefers">
            <div class="x-title">
                <div class="title">
                    猜你喜欢
                </div>
                <div class="more" @click="prefersHandel">
                    <div class="ico" :class="prefers.loading === true?'loading':''"></div>
                    <div class="text">换一换</div>
                </div>
            </div>
            <div class="box">
                <div class="item" v-for="(item,index) in prefers.data" :key="index" @click="videoHandel(item.uuid)">
                    <div class="image">
                        <img :src="item.thumbnail">
                    </div>
                    <div class="title">{{ item.translate_title }}</div>
                </div>
            </div>
        </div>

        <div class="video-container" v-for="(item,index) in video.data" :key="index">
            <div class="x-title">
                <div class="title">{{ item.title }}</div>
                <div class="more" @click="moreHandel(item.area)">更多</div>
            </div>
            <div class="box">
                <div class="item" v-for="(value,key) in item.data" :key="key" @click="videoHandel(value.uuid)">
                    <div class="image">
                        <img :src="value.thumbnail">
                    </div>
                    <div class="info">
                        <div class="title">
                            {{ value.translate_title }}
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <tabbar/>

    </div>
</template>

<script>
    import Swiper from 'swiper';
    import 'swiper/css/swiper.min.css';
    import 'swiper/js/swiper.min';
    import tabbar from '../../components/tabbar';

    export default {
        components: {
            tabbar
        },
        data() {
            return {
                banner: {
                    data: []
                },
                navigation: {
                    data: []
                },
                recommend: {
                    data: []
                },
                prefers: {
                    data: [],
                    loading: false
                },
                video: {
                    data: []
                }
            }
        },
        activated() {
            this.$store.state.searchKeyword = '';
        },
        mounted() {
            this.initBanner();
            this.initNavigation();
            this.initRecommend();
            this.initPrefers();
            this.initVideo();
        },
        methods: {
            initBanner() {
                this.$axios.get('/api/app/banner/home?platform=' + this.$store.state.platform)
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.banner.data = result;
                            setTimeout(() => {
                                new Swiper('#swiper', {
                                    direction: 'horizontal',
                                    loop: true,
                                    slidesPerView: "auto",
                                    centeredSlides: true,
                                    autoplay: true
                                });
                            }, 10);
                        }
                    });
            },
            initNavigation() {
                this.$axios.get('/api/app/home/navigation')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.navigation.data = result;
                        }
                    });
            },
            initRecommend() {
                this.$axios.get('/api/app/home/recommend')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.recommend.data = result;
                        }
                    });
            },
            initPrefers() {
                this.$axios.get('/api/app/home/prefers')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.prefers.data = result;
                            setTimeout(() => {
                                this.prefers.loading = false;
                            }, 400);
                        }
                    });
            },
            initVideo() {
                this.$axios.get('/api/app/home/data')
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.video.data = result;
                        }
                    });
            },
            prefersHandel() {
                this.prefers.loading = true;
                this.initPrefers();
            },
            navigationHandel(type, value, title) {
                switch (type) {
                    case 0:
                        this.$router.push('/category/index');
                        break;
                    case 1:
                        this.$router.push('/category/data?area=' + value.area + '&category=' + value.category + '&title=' + title);
                        break;
                }
            },
            moreHandel(area) {
                this.$router.push('/category/data?area=' + area + '&category=0');
            },
            videoHandel(uuid) {
                this.$router.push('/video/index?uuid=' + uuid)
            },
            bannderHandel(type, value) {
                switch (type) {
                    case 1:
                        window.open(value, "_blank");
                        break;
                    case 2:
                        break;
                    case 3:
                        break;
                    case 4:
                        break;
                    case 5:
                        this.$router.push(value);
                        break;
                    default:
                }
            }
        }
    }
</script>

