import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import fingerprint from 'fingerprintjs2';
import axios from './utility/axios';
import {Toast, Dialog} from 'vant';
import utils from './utility/utils';

//app
let platform = utils.getUrlKey('platform');
store.state.platform = platform ? platform : 'web';

//use
Vue.use(Toast, Dialog);

//config
Vue.config.productionTip = false;

//console
// import Vconsole from 'vconsole';
// const vConsole = new Vconsole();
// Vue.use(vConsole);

//axios
Vue.prototype.$axios = axios;

//router
Vue.prototype.$routerPush = function (url) {
    router.push(url);
};
Vue.prototype.$routerBack = function () {
    if (window.plus) {
        plus.webview.currentWebview().back();
    } else {
        router.go(-1);
    }
};

router.beforeEach((to, from, next) => {
    //游客登录
    let token = localStorage.getItem('token');
    if (!token) {
        setTimeout(function () {
            fingerprint.get(function (components) {
                let values = components.map(function (component) {
                    return component.value
                });
                let hash = fingerprint.x64hash128(values.join(''), 31);
                axios.post('/api/app/auth/guest', {
                    device: hash,
                    platform: store.state.platform,
                    system: navigator.userAgent,
                    version: '1000'
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            localStorage.setItem('token', result.token);
                            localStorage.setItem('salt', result.salt);
                            localStorage.setItem('expires', result.expires);
                            next();
                        }
                    });
            })
        }, 500);

    } else {
        next();
    }
});

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');
