import axios from 'axios';

//axios.defaults.baseURL = 'http://149.248.10.198';

export function get(url, param = null) {
    let locationToken = localStorage.getItem('token');
    let token = locationToken ? locationToken : 'NONE';

    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: param,
            headers: {
                'token': token
            }
        })
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
            .catch((error) => {
                reject(error)
            })
    });
}

export function post(url, params = null) {
    let locationToken = localStorage.getItem('token');
    let token = locationToken ? locationToken : 'NONE';
    return new Promise((resolve, reject) => {
        axios.post(url, params, {
            headers: {
                'token': token
            }
        })
            .then(response => {
                resolve(response);
            }, err => {
                reject(err);
            })
            .catch((error) => {
                reject(error)
            })
    });
}

export default {
    get,
    post
}