<script>
    import {Tabbar, TabbarItem} from 'vant';

    export default {
        name: 'tabbar',
        components: {
            [Tabbar.name]: Tabbar,
            [TabbarItem.name]: TabbarItem
        },
        data() {
            return {
                menu: {
                    active: 'home'
                },
                icon: {
                    home: {
                        normal: require(`../assets/tabbar/home.png`),
                        active: require(`../assets/tabbar/home_active.png`)
                    },
                    topic: {
                        normal: require(`../assets/tabbar/topic.png`),
                        active: require(`../assets/tabbar/topic_active.png`)
                    },
                    discover: {
                        normal: require(`../assets/tabbar/discover.png`),
                        active: require(`../assets/tabbar/discover_active.png`)
                    },
                    user: {
                        normal: require(`../assets/tabbar/user.png`),
                        active: require(`../assets/tabbar/user_active.png`)
                    }
                }
            }
        },
        activated() {
            this.menu.active = this.$route.name;
        },
        methods: {}
    };
</script>

